class SetTimeoutWorker {
    constructor() {
        // 因为 worker 的代码很短，所以直接储存在这里，避免从网络加载导致的延迟问题
        this.workerCode = `onmessage = (ev) => {
    setTimeout(() => {
      postMessage({
        id: ev.data.id
      })
    }, ev.data.time)
  }`;
        this.list = [];
        this.timerId = 0;
        this.createWorker();
    }
    createWorker() {
        const blob = new Blob([this.workerCode]);
        this.worker = new Worker(URL.createObjectURL(blob));
        this.worker.addEventListener('message', (ev) => {
            const id = ev.data.id;
            if (this.list[id].callback !== null) {
                this.list[id].callback();
                this.clear(id);
            }
        });
    }
    set(callback, time) {
        const data = {
            id: this.timerId,
            time,
            callback,
        };
        this.list.push(data);
        this.timerId++;
        this.worker.postMessage({
            id: data.id,
            time,
        });
        return data.id;
    }
    clear(id) {
        this.list[id].callback = null;
    }
}
const setTimeoutWorker = new SetTimeoutWorker();
export { setTimeoutWorker };
